import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AlertController, LoadingController, NavController, ToastController, Platform  } from '@ionic/angular';
import { AccessProviders } from '../providers/access-providers';
import { Storage } from '@ionic/storage';
import { DataService } from '../data.service';
import { NavparamService } from '../navparam.service';
import { of } from 'rxjs';

@Component({
  selector: 'app-approve-queue',
  templateUrl: './approve-queue.page.html',
  styleUrls: ['./approve-queue.page.scss'],
})
export class ApproveQueuePage implements OnInit {
  public categories = [];
  public featuredProducts = [];
  public bestSellProducts = [];
  prods: any =[];
  item_id: any;
  datastorage: any;
  name : any;
  sid: any;
  a = false;
  b = false;
  xyz: any;
  search: any="";
  var_price: any; 
  i: any=1;
  id: any=0
  device = true;  
  web = true;
  constructor(
    private data: DataService,
    private router: Router,
    private loadingController: LoadingController,
    private toastController: ToastController,
    private alertController: AlertController,
    private accessProviders: AccessProviders,
    public navController: NavController,
    private platform: Platform,
    private storage: Storage,
    private activatedRoute: ActivatedRoute,
    private navParamService: NavparamService
  ) {
    this.device = this.platform.is('mobile');
  this.web =this.platform.is('desktop');
}

  ngOnInit() {
    this.activatedRoute.params.subscribe((data : any)=>{
      this.id = data.id;
      console.log(this.id)
     });


      
    
    this.categories = this.data.getCategories();
    this.featuredProducts = this.data.getFeaturedProducts();
    this.bestSellProducts = this.data.getBestSellProducts();
    this.prods=[];
    return new Promise(resolve =>{
      let body = {
        aksi: 'get_products_vendor', 
        sid_id: this.sid 
      }
      this.accessProviders.postData(body, 'get_products.php').subscribe((res:any) => {
        if(res.success == true) {
           console.log(res.msg);
           console.log(res.result);
           for(let datas of res.result){
             this.prods.push(datas);
           }
           
        } else {
          this.presentToast('Failed'); 
        }
      });   
    });
  }
  async doSearch(){
    console.log(this.search);
    if(this.search==""){
      this.presentToast('Please type keyword');
    }else{
      const loader = await this.loadingController.create({
        message: 'Please wait',
      });
      loader.present();

      this.navParamService.setKey(this.search);
      this.router.navigate(['/search-product']);
      loader.dismiss();
      }
  }
  
  doRefresh(event) {
    console.log('Begin async operation');

    setTimeout(() => {
      console.log('Async operation has ended');
      event.target.complete();
    }, 2000);
  }
  

  ionViewWillEnter() {
    console.log(this.id)
    if(this.id == 1){
      window.location.reload();
    }
    this.navParamService.clear();
    this.storage.get('storage_xxx').then((res)=>{
      console.log(res);
        this.datastorage = res;
        this.sid=this.navParamService.getStoreId();
        if(this.sid==0){
          this.sid = this.datastorage.store_id;
        }
        this.name = this.datastorage.first_name;
        this.xyz = this.navParamService.getNavData();
        if(this.sid==0&&this.xyz==0){
          console.log('store does not exist');
          this.a=true;
          this.b=false;
        }else{
          console.log('store exist');
          this.b=true;
          this.a=false;
        }
    });
   
    
  }
  itemDetails(product_id){
    console.log(product_id); 
    this.navParamService.setNavData(product_id);
    this.router.navigate(['/admin-prods']);
  }


  navigate(path) {
    this.router.navigate([path]);
  }
  async logout(){
   
    this.alertController
    .create({
      header: "Logout",
      message: "Are you sure you want to logout?",
      buttons: [
        {
          text: "Yes",
          handler: async ()=>{
            const loader = await this.loadingController.create({
              message: 'Please wait',
            });
            loader.present();
            loader.dismiss();
            this.storage.clear();
            this.presentToast('Log out successfully');
            this.navController.navigateRoot(['/intro']);
          }
        },
        { text: "No" }  
      ]
    })
    .then(alertEL => alertEL.present());

  }

  async presentToast(a) {
    const toast = await this.toastController.create({
      message: a,
      duration: 1500,
      position: 'top'
    });
    toast.present();
  }  

}
 